@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*, :after, :before {
  box-sizing: border-box;
  overflow: hidden;
}

.orgchart {
  background-color: rgb(109, 109, 109);
  height: 1200px;
  width: 1200px;
  display: flex;
  justify-content: center;
  align-items: center
}

.employee-title {
  font-family: 'Roboto';
  font-size: 24px;
  font-weight: 500
}

.employee-children-icon {
  align-self: flex-end
}

.App {
  overflow: hidden;
  display: flex;
  height: 100vh;
  padding: 20px;
}

.InputSettings {
  width: 35%;
  height: 100%;
}

#react-org-chart {
  background-color: rgb(206, 206, 206);
  height:100vh;
  width: 100vw;
  overflow: hidden;
}

#icon {
  display: none;
}

@media only screen and (max-width: 750px) {

  .App {
    display: flex;
    flex-direction: column;
    overflow: scroll;
  }

  .InputSettings {
    width: 100%;
  }
}
*, :before, :after {
  box-sizing: border-box
}



.main-view {
  display: flex;
  padding: 15px;
  background-color: rgb(206, 206, 206);
  height: 100vh;
}

.settings {
  position: absolute;
}
